:root {
  --black: #000000;
  --white: #ffffff;
  --background: #e5e5e5;
  --grey: grey;
  --yellow: #ffdd2d;
  --button-submit: #ffde01;
  --button-brown: #333333;
  --height: 511px;
  --modal-button: #2e2ee550;
  --input: rgba(255, 255, 255, 0.55);
}
@font-face {
  font-family: "SF Pro";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/sf-pro-regular.woff2") format("woff2"), url("../../fonts/sf-pro-regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/sf-pro-medium.woff2") format("woff2"), url("../../fonts/sf-pro-medium.woff") format("woff");
}
@font-face {
  font-family: "SF Pro";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/sf-pro-bold.woff2") format("woff2"), url("../../fonts/sf-pro-bold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 900;
  src: url("../../fonts/Montserrat-Black.woff2") format("woff2"), url("../../fonts/Montserrat-Black.woff") format("woff");
}
.body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "SF Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  background-color: var(--white);
  color: var(--white);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
}
.header {
  width: 320px;
  margin: 0 auto;
  padding: 15px 20px;
  box-sizing: border-box;
}
@media (min-width: 660px) {
  .header {
    width: 660px;
  }
}
@media (min-width: 1000px) {
  .header {
    width: 1000px;
  }
}
.header__navigation {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.header__button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.header__logo-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.header__book-text,
.header__people-text {
  display: none;
  font-size: 17px;
  line-height: 25px;
  font-weight: 700;
  padding: 10px 15px;
  border-radius: 20px;
  color: var(--black);
  background-color: var(--background);
  transition: all 0.1s linear;
}
@media (min-width: 1000px) {
  .header__book-text,
  .header__people-text {
    display: block;
  }
}
.header__book-text:hover,
.header__people-text:hover {
  color: var(--white);
  background-color: var(--black);
}
.header__book-text:active,
.header__people-text:active {
  opacity: 0.3;
}
.header__book-text {
  margin-right: 20px;
}
.header__logo {
  margin-right: 20px;
  fill: var(--black);
  transition: all 0.05s linear;
}
.header__logo:hover {
  fill: var(--white);
  background-color: var(--black);
}
.header__logo:active {
  fill: var(--white);
  background-color: var(--yellow);
}
.header__find {
  fill: var(--black);
}
.header__button--one:hover .header__find {
  fill-opacity: 0.6;
}
.header__button--one:active .header__find {
  fill-opacity: 0.3;
}
.header__user {
  fill: var(--black);
}
.header__button--two:hover .header__user {
  fill-opacity: 0.6;
}
.header__button--two:active .header__user {
  fill-opacity: 0.3;
}
.main__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 660px) {
  .main__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 660px;
    margin: 0 auto;
  }
}
@media (min-width: 1000px) {
  .main__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 1000px;
    margin: 0 auto;
  }
}
.item {
  position: relative;
  width: 320px;
  min-height: var(--height);
  box-sizing: border-box;
  padding-top: 40px;
  padding-right: 20px;
  padding-bottom: 69px;
  padding-left: 20px;
  margin: 0 auto;
  margin-bottom: 20px;
  border-radius: 10px;
  background-color: var(--start);
  overflow: hidden;
}
.item:last-of-type {
  margin-bottom: 0;
}
.item:first-of-type {
  margin-bottom: 20px;
}
.item--main {
  padding-top: 52px;
  background-image: url("../../img/index/background/background-main.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -58px center;
}
@media (min-resolution: 2dppx) {
  .item--main {
    background-image: url("../../img/index/background/background-main@2x.jpg");
  }
}
.item--one {
  background-image: url("../../img/index/background/background-one.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -56px center;
}
@media (min-resolution: 2dppx) {
  .item--one {
    background-image: url("../../img/index/background/background-one@2x.jpg");
  }
}
.item--two {
  background-image: url("../../img/index/background/background-two.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -56px center;
}
@media (min-resolution: 2dppx) {
  .item--two {
    background-image: url("../../img/index/background/background-two@2x.jpg");
  }
}
.item--three {
  background-image: url("../../img/index/background/background-three.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -56px center;
}
@media (min-resolution: 2dppx) {
  .item--three {
    background-image: url("../../img/index/background/background-three@2x.jpg");
  }
}
.item--four {
  background-image: url("../../img/index/background/background-four.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -56px center;
}
@media (min-resolution: 2dppx) {
  .item--four {
    background-image: url("../../img/index/background/background-four@2x.jpg");
  }
}
.item--five {
  background-image: url("../../img/index/background/background-five.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -56px center;
}
@media (min-resolution: 2dppx) {
  .item--five {
    background-image: url("../../img/index/background/background-five@2x.jpg");
  }
}
.item--six {
  background-image: url("../../img/index/background/background-six.jpg");
  background-repeat: no-repeat;
  background-size: 320px auto;
  background-position: top -58px center;
}
@media (min-resolution: 2dppx) {
  .item--six {
    background-image: url("../../img/index/background/background-six@2x.jpg");
  }
}
.item__date {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 14px;
  line-height: 17px;
  border-radius: 24px;
  color: var(--black);
  background-color: var(--yellow);
}
.item__heading {
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
}
.item__heading--main {
  margin-top: 20px;
  font-size: 26px;
  line-height: 31px;
}
.item__description {
  margin: 0;
  padding: 0;
  margin-bottom: 23px;
  font-size: 16px;
  line-height: 20px;
}
.item__description--main {
  font-size: 18px;
  line-height: 22px;
}
.item__link {
  color: var(--yellow);
  border-bottom: 1px solid var(--yellow);
  transition: opacity 0.1s linear;
}
.item__link:hover {
  opacity: 0.6;
}
.item__link:active {
  opacity: 0.3;
  border-bottom: none;
}
.item__link:focus {
  opacity: 0.6;
}
.item__content-list {
  list-style-type: none;
  padding-left: 24px;
}
.item__content-item {
  position: relative;
}
.item__content-item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -14px;
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: var(--white);
}
.button {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: 0 auto;
  padding: 13px 100px;
  font-size: 15px;
  line-height: 18px;
  color: var(--button-brown);
  background-color: var(--button-submit);
  border: none;
  border-radius: 35px;
  transition: all 0.1s linear;
  cursor: pointer;
}
.button:hover {
  background-color: var(--black);
  color: var(--white);
}
.button:active {
  background-color: var(--black);
  color: var(--yellow);
  opacity: 0.3;
}
.button:focus {
  background-color: var(--black);
  color: var(--white);
}
.button--youtube {
  padding: 13px 62px;
  background-color: var(--white);
}
.input {
  box-sizing: border-box;
  width: 280px;
  min-height: 61px;
  padding: 10px 12px;
  font-size: 14px;
  line-height: 16px;
  border: none;
  border-radius: 12px;
  background-color: var(--input);
  resize: none;
  transition: background-color 0.1s linear;
}
.input::placeholder {
  font-size: 13px;
}
.input:hover {
  background-color: var(--white);
}
.input:active {
  background-color: var(--white);
}
.input:focus {
  background-color: var(--white);
}
.modal {
  position: fixed;
  top: calc(50vh - 1 / 2 * 270px);
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 300px;
  min-height: 300px;
  padding: 15px;
  box-sizing: border-box;
  color: var(--black);
  background-color: var(--white);
  border: 3px solid var(--black);
  border-radius: 10px;
}
@media (min-width: 660px) {
  .modal {
    width: 400px;
    padding: 20px;
  }
}
@media (min-width: 1000px) {
  .modal {
    width: 500px;
    padding: 25px;
  }
}
.modal--hidden {
  display: none;
}
.modal--open {
  display: block;
  animation: bounce 0.6s;
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.modal__text {
  margin-bottom: 30px;
}
.modal__form {
  display: flex;
  flex-direction: column;
}
.modal__input {
  padding: 10px 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  border: 1px solid var(--background);
  outline: none;
}
.modal__input:hover {
  border: 1px solid var(--black);
}
.modal__input:active {
  border: 1px solid var(--modal-button);
}
.modal__input:focus {
  border: 1px solid var(--modal-button);
}
.modal__button {
  padding: 10px 20px;
  margin-bottom: 10px;
  border-radius: 10px;
  border: none;
  color: var(--white);
  background-color: var(--modal-button);
  cursor: pointer;
  transition: all 0.1s linear;
}
.modal__button:hover {
  background-color: var(--yellow);
  color: var(--black);
}
.modal__button:active {
  opacity: 0.3;
}
.modal__close {
  position: absolute;
  right: 20px;
  top: 20px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal__close:hover {
  opacity: 0.6;
}
.modal__close:active {
  opacity: 0.3;
}
.modal__img {
  fill: var(--black);
}
.footer {
  width: 320px;
  margin: 0 auto;
  padding: 40px 0;
}
@media (min-width: 660px) {
  .footer {
    padding: 20px;
    width: 660px;
    box-sizing: border-box;
  }
}
@media (min-width: 1000px) {
  .footer {
    width: 1000px;
  }
}
.footer__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 660px) {
  .footer__list {
    justify-content: space-evenly;
  }
}
@media (min-width: 1000px) {
  .footer__list {
    justify-content: space-between;
  }
}
.footer__item {
  margin-left: 10px;
  margin-bottom: 5px;
}
@media (min-width: 660px) {
  .footer__item {
    margin-right: 20px;
    padding: 20px 0;
  }
}
@media (min-width: 1000px) {
  .footer__item {
    padding: 20px 0;
  }
}
.footer__link {
  position: relative;
  padding: 10px 20px;
  color: var(--black);
  opacity: 0.5;
}
.footer__link:hover {
  opacity: 0.7;
}
.footer__link:active {
  opacity: 0.9;
}
@media (min-width: 660px) {
  .footer__link {
    padding: 20px 0;
  }
}
@media (min-width: 1000px) {
  .footer__link {
    padding: 20px 0;
  }
}
